import React, { useState } from 'react';
import Popup from "reactjs-popup";
import './Card.css';
import Fade from 'react-reveal/Fade';


class Card extends React.Component {
    render() {
      const {name, role, linkedin, website, imgSrc, year, email, tree, leaf, blurb} = this.props;

      const links = {
        email: {
          href: `mailto:${email}`,
          img: 'email_green.svg'
        },
        linkedin: {
          href: linkedin,
          img: 'linkedin_green.svg'
        },
        website: {
          href: website,
          img: 'web_green.svg'
        }
      }

      const getLinks = () => {
        return (
          <div className='profile-link-container'>
          {Object.keys(links).map(link => {
            if (this.props[link]) {
              return (
                <a className="profile-link" href={links[link].href} target="_blank" rel="noopener noreferrer">
                  <img src={require(`../../images/logos/${links[link].img}`)} alt={link} />
                </a>
              );
            } else return <></>;
          })}
          </div>
        )
      }

      return (
        <Fade>
          <Popup modal trigger={
          <div className="cardSheet">
            {/* Card info  */}
              <Mustache name={name} email={email} width={160} height={175}>
                <img className="cardImg" src={imgSrc} alt={name} />
              </Mustache>
              <div className='cardNameContainer'>
                <b className="cardName midtext">{name}</b>
                <b className="cardRole">{role}</b>
              </div>
                    
            </div>} 
        position="top center">

          {/* Profile popup content */}
          <div className="profile-background">
            <h2 className="profile-name">{name}</h2>
            <h3 className='profile-role'>{role}</h3>
            <Mustache name={name} email={email} width={200} height={200}>
              <img className="profile-img" src={imgSrc} alt={name}/>
            </Mustache>
            {getLinks()}
            <p className="profile-blurb">{blurb} </p>

            {/*
            <p className="profile-text">
              <span className="profile-category">Year:</span> {year} <br/>
              {tree && <><span className="profile-category">Tree:</span> {tree} <br/></>}
              {leaf && <><span className="profile-category">Leaf:</span> {leaf}<br/></>}
              <br/>

              <p className="profile-blurb">{blurb} </p>
            </p>
            */}
          </div>
        </Popup>
        </Fade>
      );
    }
  }

  export default Card;

// Incredibly important to have this feature. Before removing it, consider the following:
   // 1. If you remove this, you will break the easter egg
   // 2. The easter egg is a very important feature
   // 3. Important features should not be removed
   // 4. Therefore, this feature should not be removed
   // 5. QED

function Mustache({name, email, width, height, children}) {

  let identifier = `${name}-${email}`;

  let [ mustacheCoords, setMustacheCoords ] = useState(_mustacheHelper[identifier]);

  let url = `https://dodo.ac/np/images/d/d4/Handlebar_Mustache_NH_Icon.png`;
  let goldenUrl = `https://cdn.shopify.com/s/files/1/1794/1127/products/GoldStacheProp_be8c95f0-a972-46c1-8fcb-3de043fb4e99_1024x1024.png?v=1571439321`;

  let mustache = <></>;

  if (mustacheCoords) {

    console.log(mustacheCoords);

    let x = parseInt(mustacheCoords.x * width);
    let y = parseInt(mustacheCoords.y * height);

    if (width != 200) {
      x -= 5;
      y -= 5;
    }

    if (name == "Kevin Nguyen") {
      url = goldenUrl;
      x -= 4;
      y += 20;
    };

    if (document.body.clientWidth <= 750) {
      x -= 15;
      y -= 10;
    }

    mustache = <img src={url} alt="mustache" style={{pointerEvents: "none", width: "45px", position: "absolute", top: y+"px", left: x+"px"}}/>;
  
  }

  const clickImageEasterEgg = event => {

    const { width, height } = event.nativeEvent.target.getBoundingClientRect();

    if (width != 200) return;

    let ox = event.nativeEvent.offsetX / width;
    let oy = event.nativeEvent.offsetY / height;

    let coords = {
      x: ox + 0.01,
      y: oy + 0.01
    }

    setMustacheCoords(coords);

    _mustacheHelper[identifier] = coords;
    console.log(JSON.stringify(_mustacheHelper));
  }

  return (
    <div style={{position: "relative"}} onClick={clickImageEasterEgg}>
      {mustache}
      {children}
    </div>
  )

}

